import React, { useEffect, useState } from "react";
import "./grid.css";
import {
  faBorderAll,
  faEllipsisV,
  faGlobeAsia,
  faPlay,
  faShareAlt,
  faRedo,
} from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import Editor from "./Editor";
import OutputScreen from "./Output_Screen";
import configData from "../config/config.json";
import PasteClient from "pastebin-api";
import { Modal } from "@material-ui/core";
import Sharelink from "./share_link";
import axios from "axios";

const Grid = () => {
  const [html, setHtml] = useState("");
  const [css, setCss] = useState("");
  const [js, setJs] = useState("");
  const [saved_url, setSavedUrl] = useState("Loading");

  const [srcDoc, setSrcDoc] = useState("");

  const [page, setPage] = useState("HTML");
  const [isShare,setShare]=useState(false);
  function reload(){
    setSrcDoc(`
        <html>
          <body>${html}</body>
          <style>${css}</style>
          <script>${js}</script>
        </html>
      `);
  }



  function share_code(){
    let data = new FormData();
    const code={
        "employees":html,
        "js":js,
        "css":css
    };
    data.append("api_paste_code",code )
    data.append("api_paste_private", 1)
    data.append("api_dev_key", configData.api_dev_key)
    data.append("api_paste_name", configData.api_paste_name)
    data.append("api_paste_expire_date", configData.api_paste_expire_date)
    // data.append("api_user_key", configData.api_user_key)
    data.append("api_paste_format", configData.api_paste_format)
    data.append("api_option", configData.api_option)

    console.log(data);
    const config = {
      headers: {
        "Access-Control-Allow-Origin": "*"
      },
    };
    
    axios
      .post(`${configData.api_create_page}`, data, config)
      .then((response) => {
        console.log(response.data);
        setSavedUrl(response.data);
      })
      .catch((error) => {
        console.log(error);
        alert("There was an error!", error);
      });
}

  async function save_code() {
    var xhttp = new XMLHttpRequest();
    xhttp.withCredentials = true;

    xhttp.onload = function() {
      if (xhttp.status != 200) { // HTTP error?
        // handle error
        alert( 'Error: ' + xhttp.status);
        return;
      }
      alert( 'Success: ' + xhttp.status);
      // get the response from xhr.response
    };

    const code={
      "html":html,
      "js":js,
      "css":css
    }

  }

  useEffect(() => {
    const timeout = setTimeout(() => {
      setSrcDoc(`
        <html>
          <body>${html}</body>
          <style>${css}</style>
          <script>${js}</script>
        </html>
      `);
    }, 500);

    return () => clearTimeout(timeout);
  }, [html, css, js]);

  return (
    <div className="grid">
      <div className="col ">
        <div className="col nav">
          <div className="col nav-left">
            <i  className="col"><img style={{objectFit:"contain"}} src="https://www.dyte.io/images/Dyte-Logo.svg"></img></i>

          </div>
          {/* <div className="col nav-right">
            <FontAwesomeIcon className="col" icon={faEllipsisV} />
            <FontAwesomeIcon className="col" icon={faGlobeAsia} />
          </div> */}
        </div>
        <div className="col files">
          <div className="col">
            <h4>Online Html Editor</h4>
          </div>
          <div className="col " onClick={() => {setPage("HTML");}}>
            <h4>HTML</h4> 
            <p>index.html</p>
          </div>
          <div className="col " onClick={() => {setPage("JS");}}>
            <h4 >JS</h4>
            <p >index.js</p>
          </div>
          <div className="col " onClick={() => {setPage("CSS");}}>
            <h4 >CSS</h4>
            <p >index.css</p>
          </div>
        </div>
      </div>
      <div className="col ">
        <div className="col nav" style={{ backgroundColor: "#151515" }}>
          <div className="col nav-left">
            <div className="editor-title" onClick={() => {setPage("HTML")}}>
              index.html
              <button >
                <FontAwesomeIcon onClick={()=>setHtml('')} className="col" icon={faRedo} />
              </button>
            </div>
            <div className="editor-title" onClick={() => {setPage("JS")}}>
            index.js
              <button  >
                <FontAwesomeIcon onClick={()=>setJs('')}  className="col" icon={faRedo} />
              </button>
            </div>
            <div className="editor-title" onClick={() => {setPage("CSS")}}>
            index.css
              <button >
                <FontAwesomeIcon className="col" onClick={()=>setCss('')} icon={faRedo} />
              </button>
            </div>
          </div>
        </div>
        <div className="col code ">
          <div className="editor-container">
            <small>
          {page === "HTML" ? (
            <Editor
              title="index.html"
              language="xml"
              value={html}
              onChange={setHtml}
            />
          ) : (
            <></>
          )}
          {page === "CSS" ? (
            <Editor
              title="index.css"
              language="css"
              value={css}
              onChange={setCss}
            />
          ) : (
            <></>
          )}
          {page === "JS" ? (
            <Editor
              title="index.js"
              language="javascript"
              value={js}
              onChange={setJs}
            />
          ) : (
            <></>
          )}
            </small>
          </div>
        </div>
      </div>
      <div className="col ">
        <div className="col nav">
          <div className="col nav-left" style={{color:"lightgreen"}}>Live View</div>
          <div className="col nav-right">
            <FontAwesomeIcon onClick={()=>{save_code()}} className="col" icon={faShareAlt} />
            <FontAwesomeIcon onClick={()=>{reload()}} className="col"  icon={faPlay} />

          </div>
        </div>
        <div className="col output">
        <OutputScreen  srcDoc={srcDoc} />
          <Modal open={isShare} onClose={() => setShare(false)}>
          <Sharelink url={saved_url} />
          </Modal>
        </div>
      </div>
    </div>
  );
};

export default Grid;
